import React from 'react'
import BookStand from './BookStand'
import './BookRow.css'
import PencilImage from '../images/pencil.png'
import BookImage from '../images/book.png'
import StarImage from '../images/star.png'
import Loading from '../images/loading2.gif'

function BookRow (props) {
  const { title, author, numPages, avgRating, prices, image_url } = props.book

  return <tr>
    <td><BookStand bookImg={image_url}/></td>
    <td>
      <p>{title}</p>
      <div className={'bookDetails'}>
        <div className={'bookAuthor'}>
          <img className='bookStandIcon' src={PencilImage} alt={'pencil'}/>
          <p>{author}</p>
        </div>
        {numPages && <div className={'bookPages'}>
          <img className='bookStandIcon' src={BookImage} alt={'book'}/>
          <p>{numPages}</p>
        </div>}
        <div className={'bookRating'}>
          <img className='bookStandIcon' src={StarImage} alt={'start'}/>
          <p>{avgRating}</p>
        </div>
      </div>
    </td>
    <td>{Object.keys(prices).map((store) => <div className={'storeNameWrapper'}><div class="storeName">{store.split('')[0]}</div></div>)}</td>
    <td>{Object.values(prices).map((price) => {
      return <div className="storePrice">{
        price === null ? <img alt={'loading'} src={Loading} className={'bookPriceLoading'}/> : typeof price === 'string' ? <p>{price}</p> :
          <p>{price} â‚¬ </p>}
      </div>
    })}
    </td>
  </tr>
}

export default BookRow
