import React, { useState } from 'react'
import { bindActionCreators } from 'redux'
import { routerActions } from 'react-router-redux'
import { connect } from 'react-redux'
import { useDispatch } from 'react-redux'
import { getBooks } from '../actions/books'
import { socketRegister } from '../utils/socket'
import Search from './Search'
import './style.css'
import BookRow from './BookRow'
import Loading from './Loading'
import LinkedinImg from '../images/linkedin.png'
import GithubImg from '../images/github.png'
import StackImg from '../images/stack.png'
import BookDep from '../images/bookdep.png'
import Wook from '../images/wook.jpg'
import Fnac from '../images/fnac.png'
import Bertrand from '../images/bertrand.png'
import Header from '../images/header.png'

function Books (props) {
  const [loading, setLoading] = useState(false)
  const dispatch = useDispatch()

  React.useEffect(() => {
    // dispatch(getBooks())
    socketRegister(dispatch)
  }, [dispatch])

  const loadMore = async () => {
    setLoading(true)
    await dispatch(getBooks(props.meta.currentPage + 1))
    setLoading(false)
  }

  return <div>
    <div className="topHeader">
      <div className="topHeaderContent">
        <div className={'topHeaderLeft'}>
          <p>Save Money</p>
          <p>with your books.</p>
          <div className={'headerSubTitle'}>
            <p>A new and creative way to buy</p>
            <p>your books in the most popular <span>shops</span></p>
          </div>
          <div className={'headerImages'}>
            <img alt={'book-depository'} src={BookDep}/>
            <img alt={'wook'} src={Wook}/>
            <img alt={'fnac'} src={Fnac}/>
            <img alt={'bertrand'} src={Bertrand}/>
          </div>
        </div>
        <div className={'topHeaderRight'}>
          <img alt={'header'} src={Header}/>
        </div>
      </div>
    </div>
    <div className="wrap">
      <p className={'pretitle'}>FEATURES</p>
      <p>Instantly Find The</p>
      <p>Best <span>Book Prices</span></p>
      <Search nBooks={props.books.length} setLoading={setLoading}/>
      <br/>
      {props.books.length > 0 && <p className={'pretitle'}>RESULTS</p>}
      <div className={'tableWrapper'}>
        <table className="Table-Normal">
          <thead>
          </thead>
          <tbody>
          {props.books.map(book => <BookRow book={book}/>)}
          </tbody>
        </table>
      </div>
      {loading && <Loading/>}
      <div className={'buttonWrapper'}>
        {props.meta.currentPage < props.meta.numPages && !loading &&
        <button className={'buttonLoadMore'} onClick={() => loadMore()}>MORE</button>}
      </div>
    </div>

    <footer>
      <div>
        <div className="socialNetworksFooter">
            <a target="_blank" rel="noopener noreferrer" href="https://github.com/soltex1">
              <img src={GithubImg} alt="github"/>
            </a>
            <a target="_blank" rel="noopener noreferrer" href="https://www.linkedin.com/in/ruben-santos/">
              <img src={LinkedinImg} alt="linkedin"/>
            </a>
            <a target="_blank" rel="noopener noreferrer" href="https://stackoverflow.com/users/2908330/soltex">
              <img src={StackImg} alt="stackoverflow"/>
            </a>
        </div>
        <div className="footerText"><p>copyright @ soltex 2019</p></div>
      </div>

    </footer>
  </div>
}

// Maps the state of the store to the component props.
const mapStateToProps = (state) => ({
  books: state.books,
  meta: state.meta
})

// Binds the action creators and maps them to the actions prop.
const mapDispatchToProps = (dispatch) => ({
  actions: bindActionCreators(
    Object.assign({
      getBooks,
    }, routerActions, {}),
    dispatch
  )
})

export default connect(mapStateToProps, mapDispatchToProps)(Books)
