const axios = require('axios')

// Load books
export const getBooks = (page = 1) => {
  return async (dispatch) => {
    try {

      const response = await axios(
        `https://cryptic-waters-92928.herokuapp.com/user/books?userId=53242860&page=${page}`
      )

      dispatch({
        type: 'ADD_BOOKS',
        data: response.data.books
      })

      dispatch({
        type: 'ADD_META',
        data: response.data.meta
      })

    } catch (err) {
    }
  }
}

export const updateBook = (bookDetails) => ({
  type: 'UPDATE_BOOK',
  data: bookDetails
})

export const cleanBooks = () => ({
  type: 'CLEAN_BOOKS',
  data: null
})
