const initialState = []

function booksReducer (state = initialState, action) {
  switch (action.type) {
    case 'ADD_BOOKS':
      return state.concat(action.data)

    case 'UPDATE_BOOK':
      const { bookId, bookPrice, trackerName } = action.data

      const bookIndex = state.findIndex((book) => book.id === bookId)

      if (bookIndex === -1) {
        return state
      }

      return [
        ...state.slice(0, bookIndex),
        { ...state[bookIndex], prices: { ...state[bookIndex].prices, [trackerName]: bookPrice } },
        ...state.slice(bookIndex + 1)
      ]

    case 'CLEAN_BOOKS':
      return []

    default:
      return state
  }
}

export default booksReducer
